    searchSynonyms = (filters: any, filters_global: any, limit: number, skip: number, sorting: any) => {

        this.setState({ loading_state: true });
        let table: any = [];

        let unapproved_filter = cloneDeep(filters);
        unapproved_filter.push({ columnName: 'status', value: 'unapproved' });
        getICDSynonyms(unapproved_filter, filters_global, limit, skip, sorting)
            .then(response => {
                if (response && response.error === undefined) {
                    table = this.generateDataForUnapprovedTable(
                        response.result ? response.result : [],
                    );
                    this.setState({
                        code_data_all_unapproved: table,
                        code_data_unapproved: table,
                        total_count_unapproved: response.total && response.total.count ? response.total.count : 0,
                        loading_state: false,
                    });
                } else {
                    table = this.generateDataForUnapprovedTable([]);
                    this.setState({
                        code_data_all_unapproved: table,
                        page_unapproved: 0,
                        isModalOpen: true,
                        modalTitle: 'Loading failed',
                        modalMessage: 'An error occured while loading the list of codes.',
                        loading_state: false,
                    });
                }
            }).catch(e => {
                console.log('fetch synonyms table error', e);
                table = this.generateDataForUnapprovedTable([]);
                this.setState({
                    code_data_all_unapproved: table,
                    page_unapproved: 0,
                    isModalOpen: true,
                    modalTitle: 'Loading failed',
                    modalMessage: 'An error occured while loading the list of codes.',
                    loading_state: false,
                });
            });
    }










































