    handleSearch = (e: React.ChangeEvent<HTMLInputElement>) => {
        const { value } = e.target;

        const self = this;

        if (self.state.typingTimeout) {
            clearTimeout(self.state.typingTimeout);
        }
        self.setState({
            global_filter: value,
            typing: true,
            page: 0,
            page_unapproved: 0,
            typingTimeout: setTimeout(function () {

                self.setState({
                    typing: false,
                    loading_state: true,
                    no_data_message: `Searching for ICD10 codes`
                });
                if (self.state.activeLink === 'Unapproved Synonyms') {
                    self.searchSynonyms(
                        self.state.default_filters_unapproved,
                        self.state.global_filter,
                        self.state.limit_unapproved,
                        0,
                        self.state.default_sorting_unapproved
                    )
                } else if (self.state.activeLink === 'All Diagnosis') {
                    self.setState({ activeTableSkip: 0, activeTableCurrentpage: 0 }, self.getActive);
                    // self.getActive();
                }
            }, 2000),
        });
    };





    handleClickRow = (row: any) => {

        const { activeTableRows } = this.state;
        if (!activeTableRows) return;

        let data = activeTableRows[row];

        // console.log(this.state.code_response)
        this.setState({
            code_info: data,
        }, () => this.handleOpenBlock());
    };





