const useStyles = makeStyles(() => ({
    selectedItems: {
        borderRadius: 20,
        fontSize: 13,
        backgroundColor: '#d1d4db',
        padding: '5px 10px'
    },
    xIcon: {
        fontSize: 16
    },
    input: {
        marginBottom: "2px"
    },
    inputLabel: {
        color: '#272e4c',
        fontSize: '12px',
        marginBottom: '5px',
        marginTop: '20px'
    },
}));
console.log(useStyles);

interface IAddSynonymsBodyProps {
    onClose: () => void;
    data: any
    addSynonym: (payload: any) => void;
}

interface IAddSynonymsBodyState {
    textFieldValue: string;
    menuTabs: any;
    activeLink: any;
}

export class AddSynonymsBody extends React.Component<IAddSynonymsBodyProps, IAddSynonymsBodyState> {
    constructor(props) {
        super(props);

        const menuItems = [
            "All",
            "Unapproved",
            "Active",
            "Inactive"
        ]

        this.state = {
            textFieldValue: '',
            menuTabs: menuItems,
            activeLink: 'All',
        }
    }

    componentDidUpdate(prevProps) {
        if (prevProps.data !== this.props.data) {
            console.log(this.props.data)
        }
    }

    addSynonym = () => {
        if (!this.props.data || !this.props.data.full_code || !this.state.textFieldValue) return;
        const synonym = { full_code: this.props.data.full_code, synonyms: [this.state.textFieldValue] }
        this.setState({ textFieldValue: '' });

        const payload = { approve: [synonym] }
        console.log('PAYLOAD', payload)

        this.props.addSynonym(payload);

    }


    handleMenuClick = async (item, index) => {
        this.setState({
            activeLink: item
        })

        console.log(item)
        console.log(index)

    }


    public render(): JSX.Element {


        return (
            <>
                <div style={{ minHeight: 600, width: 500 }}>
                    <b>
                        <span style={{
                            fontSize: '16px'
                        }}>
                            {this.props.data.full_code}
                        </span><br />
                        <span style={{
                            fontSize: '18px'
                        }}>
                            {this.props.data.full_description}
                        </span>
                    </b>

                    <hr />
                    <Grid item container justify="space-between">
                        <Grid container item xs={8}>
                            <InputLabel style={{
                                color: '#272e4c',
                                fontSize: '12px',
                                marginBottom: '5px',
                                marginTop: '5px'
                            }}>
                                Add a Synonym
                                    </InputLabel>

                            <TextField type="text"
                                style={{
                                    width: '335px',
                                    marginBottom: "2px"
                                }}

                                value={this.state.textFieldValue}
                                required
                                id="outline-add-synonyms"
                                variant="outlined"
                                onChange={(e: any) => this.setState({ textFieldValue: e.target.value })} />
                        </Grid>
                        <Grid container item xs={4} justify="flex-end">
                            <Button
                                style={{
                                    height: '40px',
                                    width: '140px',
                                    fontSize: '14px',
                                    color: '#fff',
                                    marginTop: '22px'
                                }}
                                variant="contained"
                                color="primary"
                                onClick={this.addSynonym}
                            >
                                Add Synonym
                            </Button>
                        </Grid>
                    </Grid>

                    <Grid
                        item
                        xs={12}
                        md={12}
                        style={{
                            paddingRight: '8px',
                            marginTop: 15,
                        }}>

                        <Grid item xs={12} md={12} style={{ marginLeft: '8px' }}>
                            <ul style={{ padding: '0', margin: '0' }}>
                                {this.state.menuTabs.map((item, i) => (
                                    <li style={{
                                        display: "inline-block",
                                        padding: "10px 30px 10px 0"
                                    }}>
                                        <Link
                                            style={this.state.activeLink === item ? {
                                                color: "#3ab77d",
                                                fontWeight: "bold",
                                                textDecoration: "underline"
                                            } : {
                                                    color: "#929292",
                                                    display: "block",
                                                    fontSize: "1.10em",
                                                }
                                            }
                                            onClick={() => this.handleMenuClick(item, i)}
                                        >
                                            {item}(0)
                                        </Link>
                                    </li>
                                ))
                                }
                            </ul>
                        </Grid>

                    </Grid>

                    <hr />

                    {/* <Grid item container justify="space-between" style={{ marginTop: '250px' }}>
                        <Grid container item xs={8}>

                        </Grid>
                        <Grid container item xs={4} justify="flex-end">
                            <Button
                                style={{
                                    height: '40px',
                                    width: '140px',
                                    fontSize: '14px',
                                    color: '#fff',
                                    marginTop: '22px'
                                }}
                                variant="contained"
                                color="primary"
                                onClick={this.addSynonym}
                            >
                                Save
                            </Button>
                        </Grid>
                    </Grid> */}

                </div>

            </>
        )
    }

}
